//z-index
@for $i from 1 through 10 {
  .z-#{$i} {
    z-index: #{$i} !important;
  }
}

// grid stuff
.d-grid {
  display: grid;
}
@for $i from 1 through 10 {
  .gcs-#{$i} {
    grid-column-start: $i;
  }
  .gce-#{$i} {
    grid-column-end: $i;
  }
  .grs-#{$i} {
    grid-row-start: $i;
  }
  .gre-#{$i} {
    grid-row-end: $i;
  }
}

